import { Box } from "@chakra-ui/react";
import BgImg from "../components/bgImg.png";
// import starImg from "public_assets/img/starImg.png";
import "./Success.css";

import { Link, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";

const Failure = () => {
  const location = useLocation();

  console.log(location);

  const index = location.state.index;
  console.log(index);

  const [showCompleted, setShowCompleted] = useState(false);

  // 5      
  useEffect(() => {
    const timer = setTimeout(() => {
      setShowCompleted(true); // 5    
    }, 2000);

    //  unmount    
    return () => clearTimeout(timer);
  }, []);

  const arrest = () => {
    switch (index) {
      case 1:
      case 4:
      case 6:
      case 7:
      case 8:
      case 9:
        return " .";

      case 2:
      case 3:
        return "  .";
    }
  };

  const reason = () => {
    switch (index) {
      case 1:
      case 4:
      case 6:
      case 7:
      case 8:
        return "   .";

      case 2:
        return "  .";

      case 3:
        return "  .";
      case 9:
        return "     .";
    }
  };
  return (
    <>
      {!showCompleted ? (
        <Box
          maxW={"450px"}
          // p={"20px"}
          maxH={"900px"}
          h={"100vh"}
          bgImage={`url(${BgImg})`}
          bgRepeat={"no-repeat"}
          bgPosition={"center"}
          m={"0 auto"}
          display={"flex"}
          flexDirection={"column"}
          alignItems={"center"}
        >
          <Box
            width={"90%"}
            border={"1px solid black"}
            backgroundColor={"white"}
            p={"10px"}
            borderRadius={"10px"}
            borderBottom={"3px solid black"}
            marginTop={"50px"}
          >
            <Box
              display={"flex"}
              flexDirection={"column"}
              alignItems={"center"}
              border={"1px solid black"}
              backgroundColor={"white"}
              p={"20px"}
              textAlign={"center"}
              borderRadius={"10px"}
              borderTop={"3px solid black"}
              fontSize={"20px"}
              fontWeight={"500"}
            >
              <Box w={"100%"}>{arrest()}</Box>
              <Box w={"100%"} fontSize={"14px"} marginTop={"40px"}>
                 : {reason()}
              </Box>
            </Box>
          </Box>
          <Link to={"/"}>
            <Box w={"100%"} as="h4" fontSize={"20px"} marginTop={"15px"}>
              
            </Box>
          </Link>
        </Box>
      ) : (
        <Box
          maxW={"450px"}
          // p={"20px"}
          maxH={"900px"}
          h={"100vh"}
          bgImage={`url(${BgImg})`}
          bgRepeat={"no-repeat"}
          bgPosition={"center"}
          m={"0 auto"}
          display={"flex"}
          flexDirection={"column"}
          alignItems={"center"}
        >
          <Box
            width={"90%"}
            border={"1px solid black"}
            backgroundColor={"white"}
            p={"10px"}
            borderRadius={"10px"}
            borderBottom={"3px solid black"}
            marginTop={"50px"}
          >
            <Box
              display={"flex"}
              flexDirection={"column"}
              alignItems={"center"}
              border={"1px solid black"}
              backgroundColor={"white"}
              p={"20px"}
              textAlign={"center"}
              borderRadius={"10px"}
              borderTop={"3px solid black"}
              fontSize={"20px"}
              fontWeight={"500"}
            >
              <Box
                w={"100%"}
                // h={"50px"}
                bgImg={`url(${process.env.PUBLIC_URL}/img/starImg.png)`}
                bgRepeat={"no-repeat"}
                bgSize={"contain"}
              ></Box>

              <Box
                // marginTop={"20px"}
                display={"flex"}
                w={"80px"}
                justifyContent={"space-between"}
                alignItems={"center"}
              >
                <Box className="loading" w={"40px"} fontSize={"60px"} as="h5">
                  !
                </Box>
                <Box className="loading" w={"40px"} fontSize={"60px"} as="h5">
                  !
                </Box>
                <Box fontSize={"60px"} className="loading" w={"40px"} as="h5">
                  !
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      )}
    </>
  );
};

export default Failure;
