import { Box } from "@chakra-ui/react";
import BgImg from "../components/bgImg.png";
import { useNavigate } from "react-router-dom";
import { useEffect, useRef, useState } from "react";
import { Typed } from "react-typed";

const Desc = () => {
  const navigate = useNavigate();
  useEffect(() => {
    const timer = setTimeout(() => {
      navigate("/options");
    }, 3000);

    return () => clearTimeout(timer);
  }, [navigate]);

  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const typedRef = useRef(null);
  const typedRefSecond = useRef(null);
  const [showSecondTyped, setShowSecondTyped] = useState(false);

  useEffect(() => {
    if (typedRef.current) {
      new Typed(typedRef.current, {
        strings: [
          "        .      .    , , ,  .  ...      .",
        ],
        typeSpeed: 50,
        loop: false,
        showCursor: false,
        onComplete: () => {
          setShowSecondTyped(true);
        },
      });
    }
  }, []);

  useEffect(() => {
    //   Typed  
    if (showSecondTyped && typedRefSecond.current) {
      new Typed(typedRefSecond.current, {
        strings: [
          "!            .            . !    ,  !",
        ],
        typeSpeed: 50,
        loop: false,
        showCursor: false, //  
        onComplete: () => {
          setTimeout(() => {
            navigate("/options");
          }, 3000);
        },
      });
    }
  }, [showSecondTyped]); //         

  return (
    <Box
      maxW={"450px"}
      // p={"20px"}
      maxH={"900px"}
      h={"100vh"}
      bgImage={`url(${BgImg})`}
      bgRepeat={"no-repeat"}
      bgPosition={"center"}
      m={"0 auto"}
      display={"flex"}
      flexDirection={"column"}
      alignItems={"center"}
    >
      <Box
        w={"90%"}
        border={"1px solid black"}
        backgroundColor={"white"}
        p={"10px"}
        borderRadius={"10px"}
        borderBottom={"3px solid black"}
        marginTop={"20px"}
      >
        <Box
          border={"1px solid black"}
          backgroundColor={"white"}
          p={"15px"}
          textAlign={"center"}
          borderRadius={"10px"}
          borderTop={"3px solid black"}
          fontSize={"14px"}
        >
          <Box fontSize="xl" color="teal.500" fontWeight="bold">
            <div ref={typedRef}></div>
          </Box>
        </Box>
      </Box>

      {showSecondTyped && (
        <Box
          w={"90%"}
          border={"1px solid black"}
          backgroundColor={"white"}
          p={"10px"}
          borderRadius={"10px"}
          borderBottom={"3px solid black"}
          marginTop={"10px"}
        >
          <Box
            border={"1px solid black"}
            backgroundColor={"white"}
            p={"15px"}
            textAlign={"center"}
            borderRadius={"10px"}
            borderTop={"3px solid black"}
            fontSize={"14px"}
          >
            <Box fontSize="xl" color="teal.500" fontWeight="bold">
              <div ref={typedRefSecond}></div>
            </Box>
          </Box>
        </Box>
      )}
    </Box>
  );
};

export default Desc;
